.viewer-content {
  background: #f8fafc;
  min-height: calc(100vh - 70px);
}

.version-tag {
  color: #64748b;
  background: #f1f5f9;
  border-radius: 9999px;
  margin-left: .5rem;
  padding: .25rem .5rem;
  font-size: .75rem;
  font-weight: 500;
}

.editor-container {
  height: 500px;
  transition: border-color .2s;
  position: relative;
  overflow: auto;
}

.editor-container pre {
  border: none;
  border-radius: 0;
  height: 95%;
  margin: 0;
  background: #fff !important;
  padding: 1.5rem !important;
}

.editor-container code {
  outline: none;
  height: 100%;
  font-family: Fira Code, monospace !important;
  font-size: .875rem !important;
  line-height: 1.6 !important;
}

.preview-container {
  background: #f8fafc;
  height: 500px;
  margin: 2rem 1rem;
  padding: 1.5rem;
  overflow: auto;
}

.card {
  border: none;
  border-radius: .75rem;
  overflow: hidden;
}

.card-header {
  border-bottom: 1px solid #e2e8f0;
}

.card-header h5 {
  color: #1f2937;
  font-weight: 600;
}

.card-body {
  overflow: hidden;
  padding: 0 !important;
}

.btn-outline-primary {
  color: #4f46e5;
  border-color: #e2e8f0;
}

.btn-outline-primary:hover {
  color: #fff;
  background-color: #4f46e5;
  border-color: #4f46e5;
}

.btn-primary {
  background-color: #4f46e5;
  border-color: #4f46e5;
}

.btn-primary:hover {
  background-color: #4338ca;
  border-color: #4338ca;
}

.form-select {
  color: #1f2937;
  border-color: #e2e8f0;
}

.form-select:focus {
  border-color: #4f46e5;
  box-shadow: 0 0 0 .25rem #4f46e540;
}

.editor-container::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

.preview-container::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

.editor-container::-webkit-scrollbar-track {
  background: #f1f5f9;
}

.preview-container::-webkit-scrollbar-track {
  background: #f1f5f9;
}

.editor-container::-webkit-scrollbar-thumb {
  background: #cbd5e1;
  border-radius: 4px;
}

.preview-container::-webkit-scrollbar-thumb {
  background: #cbd5e1;
  border-radius: 4px;
}

.editor-container::-webkit-scrollbar-thumb:hover {
  background: #94a3b8;
}

.preview-container::-webkit-scrollbar-thumb:hover {
  background: #94a3b8;
}

.validation-feedback {
  z-index: 10;
  background: #fff;
  border-radius: .5rem;
  margin-top: 1.5rem;
  padding: 1rem;
  display: none;
  position: relative;
  box-shadow: 0 1px 3px #0000001a;
}

.validation-feedback.show {
  display: block;
}

.validation-feedback i {
  margin-right: .5rem;
  font-size: 1.1em;
}

.validation-details {
  margin-top: .5rem;
  margin-bottom: 0;
  padding-left: 1.5rem;
  font-size: .9em;
  list-style: none;
}

.validation-details li {
  align-items: center;
  margin-bottom: .25rem;
  display: flex;
}

.validation-details li:before {
  content: "•";
  color: currentColor;
  margin-right: .5rem;
}

.valid-feedback, .invalid-feedback {
  display: none;
}

.valid-feedback {
  color: #10b981;
  align-items: flex-start;
  display: flex;
}

.invalid-feedback {
  color: #ef4444;
  align-items: flex-start;
  display: flex;
}

.validation-feedback.show.is-valid .valid-feedback {
  display: flex !important;
}

.validation-feedback.show.is-valid .invalid-feedback, .validation-feedback.show.is-invalid .valid-feedback {
  display: none !important;
}

.validation-feedback.show.is-invalid .invalid-feedback {
  display: flex !important;
}

.editor-container.is-valid {
  border-color: #10b981;
}

.editor-container.is-invalid {
  border-color: #ef4444;
}

#previewContainer {
  padding: 1.5rem 0;
}

#previewContainer > div {
  margin: 0 auto;
}
